.rows-container {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: calc(var(--tile-size)/5);
}

.row0 {
    grid-row: 1;
}

.row1 {
    grid-row: 2;
}

.row2 {
    grid-row: 3;
}

.row3 {
    grid-row: 4;
}

.row4 {
    grid-row: 5;
}

.column0 {
    grid-column: 1;
}

.column1 {
    grid-column: 2;
}

.column2 {
    grid-column: 3;
}

.column3 {
    grid-column: 4;
}

.column4 {
    grid-column: 5;
}